<template>
  <div class="privilege">
    <headers :title="title"></headers>
    <p class="success" @click="edittq">{{edit}}</p>

    <van-row class="sytq" style="margin-top:50px;">
      <h2> {{privilege.name}} </h2>
      <van-col span="6" v-for="obj in privilege.contents" :key="obj.id">
          <div class="viewmore" :class="edit === '完成' ? 'jian' : ''">
            <img class="main-image" :src="obj.iconsUrl" alt="">
            <img v-show="edit === '完成'" class="jian" src="../assets/tqbj/jian.png" alt="" @click="del">
            <p>{{obj.title}}</p>
          </div>
      </van-col>
    </van-row>

    <van-row id="qbtq" class="sytq">
      <h2> {{privilegeAll.name}} </h2>
      <van-col span="6" v-for="obj in privilegeAll.contents" :key="obj.id" >
          <div class="viewmore" v-if="obj.isTop=='top'? true : false" :class="edit === '完成' ? 'jian' : ''">
            <img class="main-image" :src="obj.iconsUrl" alt="">
            <img v-show="edit === '完成'" class="jian" src="../assets/tqbj/right.png" alt="">
            <p>{{obj.title}}</p>
          </div>
      </van-col>

      <van-col span="6" v-for="obj in privilegeAll.contents" :key="obj.id">
          <div class="viewmore" v-if="obj.isTop=='normal'? true : false" :class="edit === '完成' ? 'jian' : ''">
            <img class="main-image" :src="obj.iconsUrl" alt="">
            <img v-show="edit === '完成'" class="jian" src="../assets/tqbj/add.png" alt="" v-on:click="add(obj.productId)">
            <p>{{obj.title}}</p>
          </div>
      </van-col>
    </van-row>
  </div>
</template>

<script>
  import "lib-flexible"
  import headers from '../components/editHeader.vue'
  import { Toast } from 'vant'
  export default {
    data() {
      return {
        title: "特权编辑",
        edit: '编辑',
        privilege: [],
        privilegeAll: []
      };
    },
    components: {
      headers
    },
    created () {
      let _this = this;
      this.$http.all([this.getPrivilege(), this.getPrivilegeAll()])
        .then(this.$http.spread(function( privilege,privilegeAll){

          _this.privilege = privilege.data.privilege;
          _this.privilegeAll = privilegeAll.data.privilegeAll;

          console.log({ privilege,privilegeAll })
          console.log('执行完成')
        }))

    },
    methods: {
      edittq() {
        if(this.edit === '编辑'){
          this.edit = '完成'
        }else {

          this.edit = '编辑'
        }
      },
      add() {

        if(this.privilege.length>7){
          Toast('首页特权不能超过7个');
        }else{

        }
      },
      del(){

      },
      changePrivilege(){

      },
      getPrivilege(){
        return this.$get('guanjia_heard/homepage/privilege/getPrivilege')
       },
       getPrivilegeAll(){
        return this.$get('guanjia_heard/homepage/privilege/getPrivilegeAll')
       }
    }
  };
</script>

<style lang="less" scoped>
  .privilege {
    flex: 1;
    background: #f6f6f6;
    .success {
      color: #fff;
      font-size: 34px;
      position: fixed;
      right: 40px;
      top: 28px;
      z-index: 10000;
    }
    .sytq{
      padding: 0 24px;
      padding-top: 30px;
      margin-bottom: 20px;
      background: #fff;
      h2{
        font-size: 36px;
        padding-left: 20px;
        padding-bottom: 36px;
      }
      .nav1 {
        width: 100%;
        display: flex;
        justify-content: space-around;
        padding-bottom: 66px;
        .more {
          display: flex;
          position: relative;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          width: 158px;
          height: 176px;
          img {
            width: 75px;
            height: 88px;
          }
          span {
            font-size: 27px;
            color: #000;
          }
          .jian{
            position: absolute;
            width: 30px;
            height: 30px;
            top: 8px;
            right: 10px;
          }
        }
        .opacity{
          opacity: 0;
        }

      }
    }
    .viewmore {
          align-items: center;
          justify-content: center;
          width: 158px;
          height: 176px;
          position: relative;
          margin:0 2px 10px 2px;
          .main-image {
            margin: 0 auto;
            display: block;
            padding-top: 14px;
          }
          img {
            width: 75px;
            height: 88px;
          }
          p {
            text-align: center;
            font-size: 27px;
            color: #000;
          }
          .jian{
            position: absolute;
            width: 30px;
            height: 30px;
            top: 0;
            right: 0;
          }
        }
    #qbtq{
      padding-top: 40px;
    }
    .jian{
          background: #f6f7f3;
    }
  }
</style>



